import React, { useState } from "react";
import { BsCheckCircle, BsCircle } from "react-icons/bs";
import {
  LanguageInterface,
  QuestionAnswerFormat,
  QuestionAnswerInterface,
  QuestionItemInterface,
  SubQuestionInterface,
} from "../../../../../../interfaces/question";
import {
  QuestionForHybrid,
  questionTypeElementsType,
} from "@mastrecruit/common";
import { MdAdd } from "react-icons/md";
import { v4 as uuidv4 } from "uuid";
import { SelectedKeysEdit } from "../EditQuestion";
import MultipleChoiceSubQuestions from "./SubQuestions/MultipleChoiceSubQuestions";
import NumericSubQuestions from "./SubQuestions/NumericSubQuestions";
import MatchingSubQuestions from "./SubQuestions/MatchingSubQuestions";

interface HybridAnsProps {
  selectedKey: SelectedKeysEdit | null;
  wholeQuestion: QuestionItemInterface[];
  selectedLanguage: LanguageInterface;
  updateAnswerHybrid: (
    question: QuestionItemInterface,
    reference_number: number,
    editedAnswer: QuestionAnswerInterface
  ) => void;
  selectedQuestionItem: QuestionItemInterface;
  selectedQuestionType: questionTypeElementsType | null;
  subQuestion: SubQuestionInterface;
  setShowLanguages: (status: boolean) => void;
}

export const FindSubQuestionData = (
  subQuestions: SubQuestionInterface[],
  reference_number: number
): SubQuestionInterface | null => {
  let findRef = subQuestions.find(
    (item) => item.reference === reference_number
  );
  if (findRef !== undefined) {
    return findRef;
  }
  return null;
};

const Hybrid = (props: HybridAnsProps) => {
  const [newChoiceData, setNewChoiceData] =
    useState<QuestionAnswerFormat | null>(null);
  const [editedChoiceData, setEditedChoiceData] = useState<{
    reference: number;
    answer: QuestionAnswerFormat;
  } | null>(null);
  const [addNewChoiceData, setAddNewChoiceData] = useState<boolean>(false);
  const [error, setError] = useState<string>("");

  const updateElementStatus = (
    itms: QuestionAnswerInterface,
    answer: QuestionAnswerFormat
  ) => {
    let newDataTest: QuestionAnswerFormat[] = [];
    for (let findData of itms.answer) {
      if (findData.id === answer.id) {
        newDataTest.push({
          id: findData.id,
          value: findData.value,
          status: !findData.status,
        });
      }
    }
    props.updateAnswerHybrid(props.selectedQuestionItem, itms.reference, {
      _id: itms._id,
      reference: itms.reference,
      answer: newDataTest,
    });
  };
  const removeElement = (
    itms: QuestionAnswerInterface,
    answer: QuestionAnswerFormat
  ) => {
    if (
      window.confirm("Are you sure do you want to remove this element?") ===
      true
    ) {
      if (
        answer.status === true &&
        FindSubQuestionData(
          props.selectedQuestionItem.sub_question,
          itms.reference
        )?.typeShort === QuestionForHybrid.SimpleCalculate
      ) {
        alert("You can not remove correct answer!");
      } else {
        let questionItems: QuestionAnswerInterface[] = [];
        for (let findDel of props.selectedQuestionItem.answers) {
          questionItems.push({
            _id: findDel._id,
            reference: findDel.reference,
            answer: findDel.answer.filter((itm) => itm.id !== answer.id),
          });
        }
        let newQuestionUpdated: QuestionItemInterface = {
          description: props.selectedQuestionItem.description,
          language: props.selectedQuestionItem.language,
          status: props.selectedQuestionItem.status,
          title: props.selectedQuestionItem.title,
          answers: questionItems,
          _id: props.selectedQuestionItem._id,
          sub_question: props.selectedQuestionItem.sub_question,
        };
        props.updateAnswerHybrid(newQuestionUpdated, itms.reference, {
          _id: itms._id,
          reference: itms.reference,
          answer: itms.answer.filter((itm) => itm.id !== answer.id),
        });
      }
    }
  };
  return (
    <div>
      {props.selectedQuestionItem.answers.length === 0 ? (
        <div className=" text-lg text-gray-400 font-extrabold">
          No answers added!
        </div>
      ) : (
        props.selectedQuestionItem.answers
          .filter((itm) => itm.reference === props.subQuestion.reference)
          .map((itms, i) => (
            <div
              key={i + 1}
              className={`bg-white ${
                props.selectedQuestionItem.answers.length === i + 1 ? "" : ""
              } px-3 py-2 mb-2`}
            >
              <div className="flex flex-row items-center justify-between gap-2">
                <div className="flex flex-row items-center gap-3 w-full">
                  <div className="w-full flex flex-col">
                    {itms.answer.map((item, t) => (
                      <div
                        key={t + 1}
                        className="w-full flex flex-row items-center justify-between"
                      >
                        {/* MultipleChoice, SingleMultiple, CalculatedMultipleChoice, TrueFalse */}
                        {(FindSubQuestionData(
                          props.selectedQuestionItem.sub_question,
                          itms.reference
                        )?.typeShort === QuestionForHybrid.MultipleChoice ||
                          FindSubQuestionData(
                            props.selectedQuestionItem.sub_question,
                            itms.reference
                          )?.typeShort === QuestionForHybrid.SingleMultiple ||
                          FindSubQuestionData(
                            props.selectedQuestionItem.sub_question,
                            itms.reference
                          )?.typeShort ===
                            QuestionForHybrid.CalculatedMultipleChoice ||
                          FindSubQuestionData(
                            props.selectedQuestionItem.sub_question,
                            itms.reference
                          )?.typeShort === QuestionForHybrid.TrueFalse) && (
                          <MultipleChoiceSubQuestions
                            selectedAnswer={item}
                            itms={itms}
                            selectedQuestionItem={props.selectedQuestionItem}
                            updateElementStatus={updateElementStatus}
                            editedChoiceData={editedChoiceData}
                            setEditedChoiceData={setEditedChoiceData}
                            updateAnswerHybrid={props.updateAnswerHybrid}
                            removeElement={removeElement}
                            setShowLanguages={props.setShowLanguages}
                          />
                        )}

                        {/* Matching */}
                        {FindSubQuestionData(
                          props.selectedQuestionItem.sub_question,
                          itms.reference
                        )?.typeShort === QuestionForHybrid.Matching && (
                          <MatchingSubQuestions
                            selectedAnswer={item}
                            itms={itms}
                            selectedQuestionItem={props.selectedQuestionItem}
                            updateElementStatus={updateElementStatus}
                            editedChoiceData={editedChoiceData}
                            setEditedChoiceData={setEditedChoiceData}
                            updateAnswerHybrid={props.updateAnswerHybrid}
                            removeElement={removeElement}
                            setShowLanguages={props.setShowLanguages}
                          />
                        )}

                        {/* Numeric, ShortAnswer, Essay, SimpleCalculate, Calculated */}
                        {(FindSubQuestionData(
                          props.selectedQuestionItem.sub_question,
                          itms.reference
                        )?.typeShort === QuestionForHybrid.Numeric ||
                          FindSubQuestionData(
                            props.selectedQuestionItem.sub_question,
                            itms.reference
                          )?.typeShort === QuestionForHybrid.ShortAnswer ||
                          FindSubQuestionData(
                            props.selectedQuestionItem.sub_question,
                            itms.reference
                          )?.typeShort === QuestionForHybrid.Essay ||
                          FindSubQuestionData(
                            props.selectedQuestionItem.sub_question,
                            itms.reference
                          )?.typeShort === QuestionForHybrid.SimpleCalculate ||
                          FindSubQuestionData(
                            props.selectedQuestionItem.sub_question,
                            itms.reference
                          )?.typeShort === QuestionForHybrid.Calculated) && (
                          <NumericSubQuestions
                            selectedAnswer={item}
                            itms={itms}
                            selectedQuestionItem={props.selectedQuestionItem}
                            updateElementStatus={updateElementStatus}
                            editedChoiceData={editedChoiceData}
                            setEditedChoiceData={setEditedChoiceData}
                            updateAnswerHybrid={props.updateAnswerHybrid}
                            removeElement={removeElement}
                            setShowLanguages={props.setShowLanguages}
                          />
                        )}
                      </div>
                    ))}

                    {FindSubQuestionData(
                      props.selectedQuestionItem.sub_question,
                      itms.reference
                    ) === null ? (
                      <div className="text-yellow-800 text-xl">
                        Invalid Answer type
                      </div>
                    ) : (
                      (FindSubQuestionData(
                        props.selectedQuestionItem.sub_question,
                        itms.reference
                      )!.typeShort === QuestionForHybrid.SingleMultiple ||
                        FindSubQuestionData(
                          props.selectedQuestionItem.sub_question,
                          itms.reference
                        )!.typeShort === QuestionForHybrid.MultipleChoice ||
                        FindSubQuestionData(
                          props.selectedQuestionItem.sub_question,
                          itms.reference
                        )!.typeShort === QuestionForHybrid.Matching) &&
                      (addNewChoiceData === false ? (
                        <div
                          onClick={() => {
                            setNewChoiceData(null);
                            setAddNewChoiceData(true);
                            props.setShowLanguages(false);
                          }}
                          className="btn bg-blue-50 text-blue-600 hover:bg-blue-100 cursor-pointer flex flex-row items-center gap-3"
                        >
                          <div>
                            <MdAdd className="text-3xl" />
                          </div>
                          <span className="text-base">Add new answer</span>
                        </div>
                      ) : (
                        <div className="bg-gray-100 rounded p-2 animate__animated animate__fadeIn animate__fast">
                          <div className="text-lg">Add new answer</div>
                          <div className="flex flex-col mt-2">
                            <textarea
                              onChange={(
                                e: React.ChangeEvent<HTMLTextAreaElement>
                              ) => {
                                setNewChoiceData({
                                  id:
                                    newChoiceData === null
                                      ? uuidv4()
                                      : newChoiceData.id,
                                  value: e.target.value,
                                  status:
                                    newChoiceData === null
                                      ? FindSubQuestionData(
                                          props.selectedQuestionItem
                                            .sub_question,
                                          itms.reference
                                        )!.typeShort ===
                                        QuestionForHybrid.Matching
                                        ? ""
                                        : false
                                      : newChoiceData.status,
                                });
                                setError("");
                              }}
                              className="input-gray-lg w-full"
                              placeholder="Type new answer"
                              value={
                                newChoiceData !== null
                                  ? newChoiceData.value
                                  : ""
                              }
                            ></textarea>
                            {FindSubQuestionData(
                              props.selectedQuestionItem.sub_question,
                              itms.reference
                            )!.typeShort === QuestionForHybrid.Matching && (
                              <textarea
                                onChange={(
                                  e: React.ChangeEvent<HTMLTextAreaElement>
                                ) => {
                                  setNewChoiceData({
                                    id:
                                      newChoiceData === null
                                        ? uuidv4()
                                        : newChoiceData.id,
                                    value:
                                      newChoiceData === null
                                        ? ""
                                        : newChoiceData.value,
                                    status:
                                      newChoiceData === null
                                        ? ""
                                        : e.target.value,
                                  });
                                  setError("");
                                }}
                                className="input-gray-lg w-full mt-2"
                                placeholder="Type new answer matching"
                                value={
                                  newChoiceData !== null
                                    ? newChoiceData.status
                                    : ""
                                }
                              ></textarea>
                            )}
                          </div>
                          <div className="flex flex-row items-center justify-between">
                            <div
                              onClick={() => {
                                setNewChoiceData(null);
                                setAddNewChoiceData(false);
                                props.setShowLanguages(true);
                              }}
                              className="btn bg-white hover:bg-gray-200 text-gray-900 cursor-pointer mt-2 mx-1"
                            >
                              Cancel
                            </div>
                            <div className="flex flex-row items-center justify-end gap-3 pt-2">
                              {FindSubQuestionData(
                                props.selectedQuestionItem.sub_question,
                                itms.reference
                              )!.typeShort !== QuestionForHybrid.Matching && (
                                <div
                                  onClick={() => {
                                    setNewChoiceData({
                                      id:
                                        newChoiceData === null
                                          ? uuidv4()
                                          : newChoiceData.id,
                                      value:
                                        newChoiceData === null
                                          ? ""
                                          : newChoiceData.value,
                                      status:
                                        newChoiceData !== null &&
                                        newChoiceData.status === true
                                          ? false
                                          : true,
                                    });
                                    setError("");
                                  }}
                                  className="flex flex-row items-center gap-2 bg-white rounded px-2 py-1 cursor-pointer"
                                >
                                  <div>
                                    {newChoiceData !== null &&
                                    newChoiceData.status === true ? (
                                      <BsCheckCircle className="text-blue-600 text-3xl" />
                                    ) : (
                                      <BsCircle className="text-gray-700 text-3xl" />
                                    )}
                                  </div>
                                  <div
                                    className={`text-base font-bold w-20 ${
                                      newChoiceData !== null &&
                                      newChoiceData.status === true
                                        ? "text-blue-600"
                                        : "text-gray-900"
                                    }`}
                                  >
                                    {newChoiceData !== null &&
                                    newChoiceData.status === true
                                      ? "Correct"
                                      : "Incorrect"}
                                  </div>
                                </div>
                              )}
                              <div
                                onClick={() => {
                                  if (newChoiceData === null) {
                                    setError("Please fill answer");
                                  } else {
                                    if (newChoiceData.value === "") {
                                      setError("Please fill answer");
                                    } else {
                                      props.updateAnswerHybrid(
                                        props.selectedQuestionItem,
                                        itms.reference,
                                        {
                                          _id: uuidv4(),
                                          reference: itms.reference,
                                          answer: [newChoiceData],
                                        }
                                      );
                                      setNewChoiceData(null);
                                      setAddNewChoiceData(false);
                                      setError("");
                                      props.setShowLanguages(true);
                                    }
                                  }
                                }}
                                className="btn bg-blue-600 hover:bg-blue-800 text-white cursor-pointer"
                              >
                                Add new
                              </div>
                            </div>
                          </div>
                          {error !== "" && (
                            <div
                              onClick={() => setError("")}
                              className="bg-red-100 hover:bg-red-200 text-red-800 px-3 py-2 rounded mt-2 text-base animate__animated animate__zoomIn animate__faster cursor-pointer"
                            >
                              {error}
                            </div>
                          )}
                        </div>
                      ))
                    )}
                  </div>
                </div>
                {/* <div className="btn bg-blue-100 text-blue-600">Add new</div> */}
              </div>
            </div>
          ))
      )}
    </div>
  );
};

export default Hybrid;
