import {
  CalculatedMultipleChoiceQuestionAnswerInterface,
  EmbeddedQuestionAnswerInterface,
  EssayQuestionAnswerInterface,
  MatchingQuestionAnswerInterface,
  MultipleChoiceQuestionAnswerInterface,
  NumericalQuestionAnswerInterface,
  ShortAnswerQuestionAnswerInterface,
  SimpleCalculatedQuestionAnswerInterface,
  TrueFalseQuestionAnswerInterface,
} from "../../../interfaces/question";

export const toMultipleChoice = (
  answers: any[]
): MultipleChoiceQuestionAnswerInterface[] => {
  let res: MultipleChoiceQuestionAnswerInterface[] = [];
  for (let answer of answers) {
    res = [
      ...res,
      {
        id: answer.id,
        value: answer.value,
        status: answer.status,
      },
    ];
  }
  return res;
};

