import React, { useState } from "react";
import { HiCheck } from "react-icons/hi";
import { ImCheckboxChecked, ImCheckboxUnchecked } from "react-icons/im";
import { MdModeEdit } from "react-icons/md";
import {
  LanguageInterface,
  QuestionItemInterface,
  QuestionLanguages,
  QuestionAnswerFormat,
  QuestionAnswerInterface,
  SubQuestionInterface,
} from "../../../../../../interfaces/question";
import { SelectedKeysEdit } from "../EditQuestion";
import { v4 as uuidv4 } from "uuid";
import { validateCalculatedFormula } from "../../../../../../utils/question_bank";
import EditorComponent from "../../../../../../components/EditorComponent/EditorComponent";
import { questionTypeElementsType } from "@mastrecruit/common";
import Embedded from "../AnswersQuestionTypes/Embedded";
import Hybrid from "../AnswersQuestionTypes/Hybrid";
import HybridSubQuestions from "./HybridSubQuestions";

interface QuestionAnswerProps {
  selectedQuestionType: questionTypeElementsType | null;
  selectedQuestionItem: QuestionItemInterface;
  wholeQuestion: QuestionItemInterface[];
  selectedLanguage: LanguageInterface;
  updateAnswerSingleMultiple: (
    question: QuestionItemInterface,
    editedAnswerIndex: string,
    editedAnswer: QuestionAnswerFormat
  ) => void;
  addAnswerSingleMultiple: (
    question: QuestionItemInterface,
    addedAnswer: QuestionAnswerFormat
  ) => void;
  removeAnswerSingleMultiple: (
    question: QuestionItemInterface[],
    removedAnswer: QuestionAnswerFormat
  ) => void;
  selectedKey: SelectedKeysEdit | null;
  setSelectedKey: (selectedKey: SelectedKeysEdit | null) => void;
  editSingleMultipleStatus: (answer_id: string, status: boolean) => void;
  marks: number;
  updateMarks: (marks: number) => void;
  updateAnswerHybrid: (
    question: QuestionItemInterface,
    reference_number: number,
    editedAnswer: QuestionAnswerInterface
  ) => void;
  setShowLanguages: (status: boolean) => void;
}

const EditHybrid = (props: QuestionAnswerProps) => {
  const [addedAnswer, setAddedAnswer] = useState<
    { lang: LanguageInterface; answer: QuestionAnswerFormat }[]
  >([]);
  const [showAddAnswerStatus, setShowAddAnswer] = useState<boolean>(false);
  const checkCalculateType =
    props.selectedQuestionType ===
    questionTypeElementsType.CalculatedMultipleChoice
      ? true
      : false;
  const AddAnswer = () => {
    if (addedAnswer !== null) {
      if (addedAnswer.length !== QuestionLanguages.length) {
        alert("Please fill question answer!" + addedAnswer.length);
      } else {
        for (let answer of addedAnswer) {
          let selectedQL = props.wholeQuestion.find(
            (itm) => itm.language === answer.lang.key
          );
          if (selectedQL !== undefined) {
            props.addAnswerSingleMultiple(selectedQL, answer.answer);
          }
        }
        setAddedAnswer([]);
        setShowAddAnswer(false);
      }
    }
  };
  const handleAnswer = (
    lang: LanguageInterface,
    answer: QuestionAnswerFormat
  ) => {
    if (addedAnswer.find((itm) => itm.lang.key === lang.key) === undefined) {
      setAddedAnswer([...addedAnswer, { lang: lang, answer: answer }]);
    } else {
      setAddedAnswer([
        ...addedAnswer.filter((itm) => itm.lang.key !== lang.key),
        { lang: lang, answer: answer },
      ]);
    }
  };

  const handleStatus = (status: boolean) => {
    if (addedAnswer.length > 0) {
      let backup = [];
      for (let itm of addedAnswer) {
        backup.push({
          lang: itm.lang,
          answer: {
            value: itm.answer.value,
            status: status,
            id: itm.answer.id,
          },
        });
      }
      setAddedAnswer(backup);
    }
  };

  const editSubQuestion = (subQuestion: SubQuestionInterface) => {
    console.log("Edited SubQuestion: ", subQuestion);
  };

  let totalMarks = 0;

  for (let tot of props.selectedQuestionItem.sub_question) {
    totalMarks += tot.marks;
  }

  return (
    <div>
      <div className="flex flex-col mb-3 text-sm gap-2 bg-white px-2 py-2 rounded">
        {showAddAnswerStatus === true && (
          <label className="text-gray-600 font-bold">Add question Answer</label>
        )}

        {showAddAnswerStatus === false ? (
          <div className="flex flex-row items-center gap-3 animate__animated animate__fadeIn animate__fast">
            <div></div>
            <div className="w-full flex flex-row items-center justify-between pr-2 py-1">
              <div className="text-gray-600 font-bold flex flex-row items-center text-2xl">
                Questions list ({props.selectedQuestionItem.sub_question.length}
                )
              </div>
              <div className="flex flex-row gap-2 items-center">
                {props.selectedKey !== null &&
                  props.selectedKey === SelectedKeysEdit.QuestionAnswer && (
                    <>
                      <div className="flex flex-row items-center justify-end gap-2">
                        <span className="font-bold text-gray-600">Marks</span>
                        <input
                          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                            props.updateMarks(parseInt(e.target.value))
                          }
                          type="number"
                          placeholder="Marks"
                          value={props.marks}
                          className="input-gray-lg h-10"
                        />
                      </div>
                      <div
                        onClick={() => props.setSelectedKey(null)}
                        className="bg-blue-100 text-blue-600 rounded-full flex items-center justify-center h-12 w-12 cursor-pointer hover:bg-blue-200 hover:text-blue-700"
                      >
                        <HiCheck className="text-3xl" />
                      </div>
                    </>
                  )}
                {props.selectedKey !== SelectedKeysEdit.QuestionAnswer && (
                  <>
                    <div className="px-4 py-2 rounded-full bg-blue-100 text-blue-600 font-bold">
                      Total Marks: {totalMarks}
                    </div>
                    {/* <div
                      onClick={() =>
                        props.setSelectedKey(SelectedKeysEdit.QuestionAnswer)
                      }
                      className="bg-gray-100 text-gray-600 rounded-full flex items-center justify-center h-12 w-12 cursor-pointer hover:bg-blue-100 hover:text-blue-600"
                    >
                      <MdModeEdit className="text-3xl" />
                    </div> */}
                  </>
                )}
              </div>
            </div>
          </div>
        ) : (
          <div className="mb-2 gap-3 bg-white rounded animate__animated animate__fadeIn animate__fast">
            <div className="flex flex-col gap-2">
              {QuestionLanguages.map((item, i) => (
                <div key={i + 1} className="flex flex-col gap-2">
                  <span className="font-bold text-blue-600">{item.value}</span>
                  {checkCalculateType === false ? (
                    <div>
                      <EditorComponent
                        key={i + 1}
                        id={"createMultiple" + (i + 1)}
                        item={
                          addedAnswer.length > 0
                            ? addedAnswer.find(
                                (itm) => itm.lang.key === item.key
                              )?.answer.value
                            : ""
                        }
                        handleItem={(data: string) => {
                          handleAnswer(item, {
                            id:
                              addedAnswer.length > 0
                                ? addedAnswer[0].answer.id
                                : uuidv4(),
                            value: data,
                            status:
                              addedAnswer.length > 0
                                ? addedAnswer[0].answer.status
                                : false,
                          });
                        }}
                      />
                    </div>
                  ) : (
                    <div className="flex flex-col w-full">
                      <textarea
                        value={
                          addedAnswer.length > 0
                            ? addedAnswer.find(
                                (itm) => itm.lang.key === item.key
                              )?.answer.value
                            : ""
                        }
                        onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) =>
                          handleAnswer(item, {
                            id:
                              addedAnswer.length > 0
                                ? addedAnswer[0].answer.id
                                : uuidv4(),
                            value: e.target.value,
                            status:
                              addedAnswer.length > 0
                                ? addedAnswer[0].answer.status
                                : false,
                          })
                        }
                        placeholder="Formula"
                        className={`${
                          checkCalculateType === true
                            ? validateCalculatedFormula(
                                addedAnswer.length > 0
                                  ? addedAnswer.find(
                                      (itm) => itm.lang.key === item.key
                                    )?.answer.value
                                  : ""
                              ).status === true
                              ? "input-gray-lg border-gray-500 focus:border-blue-600"
                              : "input-gray-lg-danger border-yellow-600 focus:border-yellow-700"
                            : "input-gray-lg"
                        }`}
                      ></textarea>
                      <span
                        className={`${
                          validateCalculatedFormula(
                            addedAnswer.length > 0
                              ? addedAnswer.find(
                                  (itm) => itm.lang.key === item.key
                                )?.answer.value
                              : ""
                          ).status === true
                            ? "text-green-600"
                            : "text-yellow-700"
                        } text-xs font-bold`}
                      >
                        {
                          validateCalculatedFormula(
                            addedAnswer.length > 0
                              ? addedAnswer.find(
                                  (itm) => itm.lang.key === item.key
                                )?.answer.value
                              : ""
                          ).msg
                        }
                      </span>
                    </div>
                  )}
                </div>
              ))}
            </div>
            <div className="flex flex-row mt-3 items-center justify-end gap-2">
              <div
                onClick={() =>
                  handleStatus(
                    addedAnswer.length > 0
                      ? !addedAnswer[0].answer.status
                      : false
                  )
                }
                className={`flex flex-row items-center gap-2 cursor-pointer ${
                  addedAnswer.length > 0 &&
                  addedAnswer[0].answer.status === true
                    ? "text-blue-600"
                    : "text-gray-600"
                } hover:text-blue-600`}
              >
                {addedAnswer.length > 0 &&
                addedAnswer[0].answer.status === false ? (
                  <ImCheckboxUnchecked className="text-2xl cursor-pointer" />
                ) : (
                  <ImCheckboxChecked className="text-2xl cursor-pointer" />
                )}
                <span className="font-bold text-xl">
                  {addedAnswer.length > 0 &&
                  addedAnswer[0].answer.status === true
                    ? "True"
                    : "False"}
                </span>
              </div>
              <div
                onClick={() => AddAnswer()}
                className="btn bg-blue-600 text-white hover:bg-blue-800 cursor-pointer w-24 text-center"
              >
                Add
              </div>
              <div
                onClick={() => setShowAddAnswer(false)}
                className="btn bg-gray-500 text-white hover:bg-gray-700 cursor-pointer w-24 text-center"
              >
                Cancel
              </div>
            </div>
          </div>
        )}
      </div>
      {props.selectedQuestionItem.sub_question.length > 0 &&
        props.selectedQuestionItem.sub_question.map((item, i) => (
          <HybridSubQuestions
            key={i + 1}
            itemNumber={i + 1}
            subQuestion={item}
            questionAnswers={props.selectedQuestionItem.answers}
            editSubQuestion={editSubQuestion}
            wholeQuestion={props.wholeQuestion}
            selectedLanguage={props.selectedLanguage}
            selectedQuestionItem={props.selectedQuestionItem}
            updateAnswerHybrid={props.updateAnswerHybrid}
            setShowLanguages={props.setShowLanguages}
          />
        ))}
    </div>
  );
};

export default EditHybrid;
