import { QuestionForHybrid } from "@mastrecruit/common";
import React, { useState } from "react";
import { IoIosArrowDown } from "react-icons/io";
import { MdModeEdit } from "react-icons/md";
import {
  LanguageInterface,
  QuestionAnswerInterface,
  QuestionItemInterface,
  SubQuestionInterface,
} from "../../../../../../interfaces";
import Hybrid from "../AnswersQuestionTypes/Hybrid";

interface HybridSubQuestionsProps {
  subQuestion: SubQuestionInterface;
  itemNumber: number;
  questionAnswers: QuestionAnswerInterface[];
  editSubQuestion: (subQuestion: SubQuestionInterface) => void;
  wholeQuestion: QuestionItemInterface[];
  selectedLanguage: LanguageInterface;
  selectedQuestionItem: QuestionItemInterface;
  updateAnswerHybrid: (
    question: QuestionItemInterface,
    reference_number: number,
    editedAnswer: QuestionAnswerInterface
  ) => void;
  setShowLanguages: (status: boolean) => void;
}

const HybridSubQuestions = (props: HybridSubQuestionsProps) => {
  const [display, setDisplay] = useState<boolean>(false);
  const [editedMarksData, setEditedMarksData] = useState<{
    reference: number;
    description: string;
    marks: string;
  } | null>(null);
  const translateClass = " transform rotate-180";
  return (
    <div>
      <div className="bg-white mb-2 rounded border">
        <div
          onClick={() =>
            (editedMarksData === null ||
              editedMarksData.reference !== props.subQuestion.reference) &&
            setDisplay(!display)
          }
          className="group flex flex-row items-center justify-between py-2 px-3 gap-3 cursor-pointer hover:bg-blue-50"
        >
          <div className="flex flex-row gap-3 items-center w-full">
            <div>
              <div className="bg-gray-100 text-blue-600 group-hover:bg-white text-2xl h-10 w-10 rounded-full flex items-center justify-center">
                {props.itemNumber}
              </div>
            </div>
            <div className="w-full">
              {editedMarksData === null ||
              editedMarksData.reference !== props.subQuestion.reference ? (
                <span className="text-lg group-hover:text-blue-700">
                  {props.subQuestion.description}
                </span>
              ) : (
                <div className="w-full">
                  <textarea
                    onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) => {
                      setEditedMarksData({
                        reference: props.subQuestion.reference,
                        description: e.target.value,
                        marks: editedMarksData.marks,
                      });
                    }}
                    className="input-gray-lg border border-gray-600 w-full"
                    value={editedMarksData.description}
                  ></textarea>
                </div>
              )}
            </div>
          </div>
          <div className="flex flex-row items-center gap-2">
            {editedMarksData === null ||
            editedMarksData.reference !== props.subQuestion.reference ? (
              <div
                onClick={() => {
                  props.selectedQuestionItem.sub_question.find(
                    (itm) => itm.reference === props.subQuestion.reference
                  ) &&
                    setEditedMarksData({
                      reference: props.subQuestion.reference,
                      description: props.subQuestion.description,
                      marks: props.selectedQuestionItem.sub_question
                        .find(
                          (itm) => itm.reference === props.subQuestion.reference
                        )!
                        .marks.toString(),
                    });
                  setDisplay(true);
                  props.setShowLanguages(false);
                }}
                className={`flex flex-row hover:text-blue-700 item-center gap-2 pl-3 cursor-pointer`}
              >
                <div className="py-1 px-3 bg-gray-100 group-hover:bg-white text-base rounded-xl font-semibold flex items-center justify-center w-max h-max">
                  {
                    props.selectedQuestionItem.sub_question.find(
                      (itm) => itm.reference === props.subQuestion.reference
                    )?.marks
                  }{" "}
                  marks
                </div>
                <div className="rounded-full flex items-center justify-center h-10 w-10 cursor-pointer bg-gray-100 group-hover:bg-white group-hover:text-blue-700">
                  <MdModeEdit className="text-2xl" />
                </div>
              </div>
            ) : (
              <div className="group flex flex-row items-center gap-2 my-1">
                <input
                  className="input-white w-full border border-gray-600"
                  type="text"
                  placeholder="Reference marks"
                  value={editedMarksData.marks}
                  onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                    setEditedMarksData({
                      reference: props.subQuestion.reference,
                      description: editedMarksData.description,
                      marks: e.target.value,
                    });
                  }}
                />
                <div
                  onClick={() => {
                    if (editedMarksData !== undefined) {
                      let currentAnswer =
                        props.selectedQuestionItem.answers.find(
                          (itm) => itm.reference === props.subQuestion.reference
                        );
                      if (currentAnswer !== undefined) {
                        let newSubQuestions: SubQuestionInterface[] = [];
                        for (let newSubQuestion of props.selectedQuestionItem
                          .sub_question) {
                          if (
                            newSubQuestion.reference ===
                            editedMarksData.reference
                          ) {
                            newSubQuestions = [
                              ...newSubQuestions,
                              {
                                _id: newSubQuestion._id,
                                description: editedMarksData.description,
                                typeShort: newSubQuestion.typeShort,
                                type: newSubQuestion.type,
                                marks: parseFloat(editedMarksData.marks),
                                reference: newSubQuestion.reference,
                              },
                            ];
                          } else {
                            newSubQuestions = [
                              ...newSubQuestions,
                              newSubQuestion,
                            ];
                          }
                        }
                        props.updateAnswerHybrid(
                          {
                            description: props.selectedQuestionItem.description,
                            language: props.selectedQuestionItem.language,
                            status: props.selectedQuestionItem.status,
                            title: props.selectedQuestionItem.title,
                            answers: props.selectedQuestionItem.answers,
                            _id: props.selectedQuestionItem._id,
                            sub_question:
                              newSubQuestions.length ===
                              props.selectedQuestionItem.sub_question.length
                                ? newSubQuestions
                                : props.selectedQuestionItem.sub_question,
                          },
                          props.subQuestion.reference,
                          currentAnswer
                        );
                        setEditedMarksData(null);
                      }
                      props.setShowLanguages(true);
                    }
                  }}
                  className="bg-blue-600 group-hover:bg-blue-800 text-white font-bold text-base flex items-center justify-center px-3 py-1 rounded cursor-pointer"
                >
                  Save
                </div>
              </div>
            )}
            <div>
              <div
                className={`bg-gray-100 group-hover:bg-white text-3xl h-10 w-10 rounded-full flex items-center justify-center transition transform${
                  display === true ? translateClass : ""
                }`}
              >
                <IoIosArrowDown />
              </div>
            </div>
          </div>
        </div>
        {display === true && (
          <div className="bg-white border-t py-3 px-3 rounded-b">
            <Hybrid
              selectedKey={null}
              wholeQuestion={props.wholeQuestion}
              selectedLanguage={props.selectedLanguage}
              updateAnswerHybrid={props.updateAnswerHybrid}
              selectedQuestionItem={props.selectedQuestionItem}
              selectedQuestionType={null}
              subQuestion={props.subQuestion}
              setShowLanguages={props.setShowLanguages}
            />
          </div>
        )}
      </div>
    </div>
  );
};

export default HybridSubQuestions;
