  updateAnswerNumericalValue = (
    question: QuestionItemInterface,
    answer_id: string,
    editedAnswer: NumericalQuestionAnswerInterface
  ) => {
    let updatedQuestion: QuestionItemInterface[] = [];
    let newEditedAnswer: NumericalQuestionAnswerInterface[] = [];
    if (editedAnswer.value.length === 1) {
      newEditedAnswer = [
        {
          id: answer_id,
          value: [
            editedAnswer.value[0],
            question.answers[0].answer[0].value[1],
          ],
          status: [
            editedAnswer.value[0] - question.answers[0].answer[0].value[1],
            editedAnswer.value[0] + question.answers[0].answer[0].value[1],
          ],
        },
      ];
    } else if (editedAnswer.status.length === 1) {
      newEditedAnswer = [
        {
          id: answer_id,
          status: [
            editedAnswer.value[0] - editedAnswer.status[0],
            editedAnswer.value[0] + editedAnswer.status[0],
          ],
          value: [editedAnswer.value[0], editedAnswer.status[0]],
        },
      ];
    } else {
      newEditedAnswer = [editedAnswer];
    }
    for (let current of this.state.question) {
      if (
        question.language === current.language ||
        getQuestionType(
          this.props.questionTypes.questionTypes,
          this.state.type
        ) === questionTypeElementsType.Numeric
      ) {
        if (
          getQuestionType(
            this.props.questionTypes.questionTypes,
            this.state.type
          ) === questionTypeElementsType.Numeric
        ) {
          updatedQuestion.push({
            _id: current._id,
            description: current.description,
            language: current.language,
            status: current.status,
            title: current.title,
            sub_question: current.sub_question,
            answers: [
              {
                _id: current.answers[0]._id,
                reference: current.answers[0].reference,
                answer: newEditedAnswer,
              },
            ],
          });
        } else {
          updatedQuestion.push({
            _id: question._id,
            description: question.description,
            language: question.language,
            status: question.status,
            title: question.title,
            sub_question: question.sub_question,
            answers: [
              {
                _id: question.answers[0]._id,
                reference: question.answers[0].reference,
                answer: newEditedAnswer,
              },
            ],
          });
        }
      } else {
        updatedQuestion.push(current);
      }
    }
    this.setState({ question: updatedQuestion });
  };

