  addAnswerSingleMultiple = (
    question: QuestionItemInterface,
    addedAnswer: QuestionAnswerFormat
  ) => {
    let updatedQuestion: QuestionItemInterface[] = [];
    let newFiltered: QuestionAnswerFormat[] = [];
    for (let current of this.state.question) {
      if (question.language === current.language) {
        newFiltered = current.answers[0].answer;
        if (
          getQuestionType(
            this.props.questionTypes.questionTypes,
            this.state.type
          ) === questionTypeElementsType.MultipleChoice
        ) {
          newFiltered.push(addedAnswer);
        }
        if (
          getQuestionType(
            this.props.questionTypes.questionTypes,
            this.state.type
          ) === questionTypeElementsType.SingleMultiple ||
          getQuestionType(
            this.props.questionTypes.questionTypes,
            this.state.type
          ) === questionTypeElementsType.CalculatedMultipleChoice
        ) {
          if (
            addedAnswer.status === true &&
            current.answers[0].answer.filter((itm) => itm.status === true)
              .length !== 0
          ) {
            newFiltered.push({
              id: addedAnswer.id,
              value: addedAnswer.value,
              status: false,
            });
          } else {
            newFiltered.push(addedAnswer);
          }
        }
        updatedQuestion.push({
          _id: question._id,
          description: question.description,
          language: question.language,
          status: question.status,
          title: question.title,
          sub_question: question.sub_question,
          answers: [
            {
              _id: question.answers[0]._id,
              reference: question.answers[0].reference,
              answer: newFiltered,
            },
          ],
        });
      } else {
        updatedQuestion.push(current);
      }
    }
    this.setState({ question: updatedQuestion });
  };

