import { QuestionForHybrid } from "@mastrecruit/common";
import React from "react";
import {
  BsCheckCircle,
  BsCheckSquareFill,
  BsCircle,
  BsSquare,
} from "react-icons/bs";
import { ImCheckboxUnchecked } from "react-icons/im";
import { IoCheckboxOutline } from "react-icons/io5";
import {
  QuestionAnswerFormat,
  QuestionAnswerInterface,
  QuestionItemInterface,
} from "../../../../../../../interfaces";
import { FindSubQuestionData } from "../Embedded";

interface MultipleChoiceSubQuestionsProps {
  selectedAnswer: QuestionAnswerFormat;
  itms: QuestionAnswerInterface;
  selectedQuestionItem: QuestionItemInterface;
  updateElementStatus: (
    itms: QuestionAnswerInterface,
    answer: QuestionAnswerFormat
  ) => void;
  editedChoiceData: {
    reference: number;
    answer: QuestionAnswerFormat;
  } | null;
  setEditedChoiceData: (
    data: {
      reference: number;
      answer: QuestionAnswerFormat;
    } | null
  ) => void;
  updateAnswerHybrid: (
    question: QuestionItemInterface,
    reference_number: number,
    editedAnswer: QuestionAnswerInterface
  ) => void;
  removeElement: (
    itms: QuestionAnswerInterface,
    answer: QuestionAnswerFormat
  ) => void;
  setShowLanguages: (status: boolean) => void;
}

const MultipleChoiceSubQuestions = (props: MultipleChoiceSubQuestionsProps) => {
  return (
    <div
      className={`group w-full flex flex-row items-center justify-between hover:bg-blue-50 ${
        FindSubQuestionData(
          props.selectedQuestionItem.sub_question,
          props.itms.reference
        )!.typeShort === QuestionForHybrid.TrueFalse
          ? ""
          : "border-b"
      } `}
    >
      <div
        onClick={() =>
          (props.editedChoiceData === null ||
            props.editedChoiceData.reference !== props.itms.reference ||
            props.editedChoiceData.answer.id !== props.selectedAnswer.id) &&
          props.updateElementStatus(props.itms, props.selectedAnswer)
        }
        className="flex flex-row items-center gap-2 w-full cursor-pointer hover:text-blue-600"
      >
        <div className="text-3xl">
          {FindSubQuestionData(
            props.selectedQuestionItem.sub_question,
            props.itms.reference
          ) === null ? (
            <div className="text-yellow-800 text-xl">Invalid Answer type</div>
          ) : FindSubQuestionData(
              props.selectedQuestionItem.sub_question,
              props.itms.reference
            )!.typeShort === QuestionForHybrid.SingleMultiple ||
            FindSubQuestionData(
              props.selectedQuestionItem.sub_question,
              props.itms.reference
            )!.typeShort === QuestionForHybrid.TrueFalse ? (
            <div>
              {props.selectedAnswer.status === true ? (
                <BsCheckCircle className="text-blue-600" />
              ) : (
                <BsCircle className="text-gray-400" />
              )}
            </div>
          ) : (
            <div>
              {props.selectedAnswer.status === true ? (
                <IoCheckboxOutline className="text-blue-700" />
              ) : (
                <BsSquare className="text-gray-500 ml-1 text-2xl" />
              )}
            </div>
          )}
        </div>
        {props.editedChoiceData !== null &&
        props.editedChoiceData.reference === props.itms.reference &&
        props.editedChoiceData.answer.id === props.selectedAnswer.id ? (
          <textarea
            onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) => {
              props.setEditedChoiceData({
                reference: props.itms.reference,
                answer: {
                  id: props.selectedAnswer.id,
                  value:
                    FindSubQuestionData(
                      props.selectedQuestionItem.sub_question,
                      props.itms.reference
                    ) === null
                      ? ""
                      : FindSubQuestionData(
                          props.selectedQuestionItem.sub_question,
                          props.itms.reference
                        )!.typeShort === QuestionForHybrid.Numeric
                      ? [
                          parseFloat(e.target.value),
                          parseFloat(props.selectedAnswer.value[1]),
                        ]
                      : e.target.value,
                  status:
                    FindSubQuestionData(
                      props.selectedQuestionItem.sub_question,
                      props.itms.reference
                    ) === null
                      ? ""
                      : FindSubQuestionData(
                          props.selectedQuestionItem.sub_question,
                          props.itms.reference
                        )!.typeShort === QuestionForHybrid.Numeric
                      ? [
                          parseFloat(e.target.value) -
                            parseFloat(props.selectedAnswer.value[1]),
                          parseFloat(e.target.value) +
                            parseFloat(props.selectedAnswer.value[1]),
                        ]
                      : props.selectedAnswer.status,
                },
              });
            }}
            placeholder="Type answer here"
            className="input-gray-lg w-full mr-2 my-1"
            value={
              FindSubQuestionData(
                props.selectedQuestionItem.sub_question,
                props.itms.reference
              ) === null
                ? ""
                : FindSubQuestionData(
                    props.selectedQuestionItem.sub_question,
                    props.itms.reference
                  )!.typeShort === QuestionForHybrid.Numeric
                ? props.editedChoiceData.answer.value[0]
                : props.editedChoiceData.answer.value
            }
          ></textarea>
        ) : (
          <div
            className={`${
              props.selectedAnswer.status === true
                ? "text-blue-800 font-bold"
                : "text-gray-800"
            } px-3 py-2 text-base`}
          >
            {FindSubQuestionData(
              props.selectedQuestionItem.sub_question,
              props.itms.reference
            ) === null
              ? ""
              : FindSubQuestionData(
                  props.selectedQuestionItem.sub_question,
                  props.itms.reference
                )!.typeShort === QuestionForHybrid.Numeric
              ? props.selectedAnswer.value[0]
              : props.selectedAnswer.value}
          </div>
        )}
      </div>
      <div className="flex flex-row items-center gap-2">
        <div
          onClick={() => {
            if (props.editedChoiceData === null) {
              props.setEditedChoiceData({
                reference: props.itms.reference,
                answer: props.selectedAnswer,
              });
              props.setShowLanguages(false);
            } else {
              console.log(props.editedChoiceData.answer);
              props.updateAnswerHybrid(
                props.selectedQuestionItem,
                props.itms.reference,
                {
                  _id: props.itms._id,
                  reference: props.itms.reference,
                  answer: [props.editedChoiceData.answer],
                }
              );
              props.setEditedChoiceData(null);
              props.setShowLanguages(true);
            }
          }}
          className="px-3 py-1 rounded bg-blue-50 text-blue-600 group-hover:bg-blue-600 group-hover:text-white cursor-pointer text-center"
        >
          Update
        </div>
        {FindSubQuestionData(
          props.selectedQuestionItem.sub_question,
          props.itms.reference
        ) === null ? (
          <div className="text-yellow-800 text-xl">Invalid Answer type</div>
        ) : (
          FindSubQuestionData(
            props.selectedQuestionItem.sub_question,
            props.itms.reference
          )!.typeShort !== QuestionForHybrid.TrueFalse && (
            <div
              onClick={() => {
                props.removeElement(props.itms, props.selectedAnswer);
              }}
              className="px-3 py-1 rounded bg-yellow-50 text-yellow-800 hover:bg-yellow-100 cursor-pointer text-center"
            >
              Remove
            </div>
          )
        )}
      </div>
    </div>
  );
};

export default MultipleChoiceSubQuestions;
