import React, { useState } from "react";
import { BsCheckCircle, BsCircle } from "react-icons/bs";
import {
  LanguageInterface,
  QuestionAnswerFormat,
  QuestionAnswerInterface,
  QuestionItemInterface,
  SubQuestionInterface,
} from "../../../../../../interfaces/question";
import { SelectedKeysEdit } from "../EditQuestion";
import {
  QuestionForEmbedded,
  questionTypeElementsType,
} from "@mastrecruit/common";
import { MdAdd, MdModeEdit } from "react-icons/md";
import { v4 as uuidv4 } from "uuid";

interface EmbeddedProps {
  selectedKey: SelectedKeysEdit | null;
  wholeQuestion: QuestionItemInterface[];
  selectedLanguage: LanguageInterface;
  updateAnswerEmbedded: (
    question: QuestionItemInterface,
    reference_number: number,
    editedAnswer: QuestionAnswerInterface
  ) => void;
  selectedQuestionItem: QuestionItemInterface;
  selectedQuestionType: questionTypeElementsType | null;
  setShowLanguages: (status: boolean) => void;
}

export const FindSubQuestionData = (
  subQuestions: SubQuestionInterface[],
  reference_number: number
): SubQuestionInterface | null => {
  let findRef = subQuestions.find(
    (item) => item.reference === reference_number
  );
  if (findRef !== undefined) {
    return findRef;
  }
  return null;
};

const Embedded = (props: EmbeddedProps) => {
  const [newChoiceData, setNewChoiceData] =
    useState<QuestionAnswerFormat | null>(null);
  const [editedChoiceData, setEditedChoiceData] = useState<{
    reference: number;
    answer: QuestionAnswerFormat;
  } | null>(null);
  const [editedMarksData, setEditedMarksData] = useState<{
    reference: number;
    marks: string;
  } | null>(null);
  const [addNewChoiceData, setAddNewChoiceData] = useState<boolean>(false);
  const [error, setError] = useState<string>("");

  const updateElementStatus = (
    itms: QuestionAnswerInterface,
    answer: QuestionAnswerFormat
  ) => {
    let newDataTest: QuestionAnswerFormat[] = [];
    for (let findData of itms.answer) {
      if (findData.id === answer.id) {
        newDataTest.push({
          id: findData.id,
          value: findData.value,
          status: !findData.status,
        });
      }
    }
    props.updateAnswerEmbedded(props.selectedQuestionItem, itms.reference, {
      _id: itms._id,
      reference: itms.reference,
      answer: newDataTest,
    });
  };
  const removeElement = (
    itms: QuestionAnswerInterface,
    answer: QuestionAnswerFormat
  ) => {
    if (
      window.confirm("Are you sure do you want to remove this element?") ===
      true
    ) {
      if (answer.status === true) {
        alert("You can not remove correct answer!");
      } else {
        let questionItems: QuestionAnswerInterface[] = [];
        for (let findDel of props.selectedQuestionItem.answers) {
          questionItems.push({
            _id: findDel._id,
            reference: findDel.reference,
            answer: findDel.answer.filter((itm) => itm.id !== answer.id),
          });
        }
        let newQuestionUpdated: QuestionItemInterface = {
          description: props.selectedQuestionItem.description,
          language: props.selectedQuestionItem.language,
          status: props.selectedQuestionItem.status,
          title: props.selectedQuestionItem.title,
          answers: questionItems,
          _id: props.selectedQuestionItem._id,
          sub_question: props.selectedQuestionItem.sub_question,
        };
        props.updateAnswerEmbedded(newQuestionUpdated, itms.reference, {
          _id: itms._id,
          reference: itms.reference,
          answer: itms.answer.filter((itm) => itm.id !== answer.id),
        });
      }
    }
  };
  return (
    <div>
      {props.selectedQuestionItem.answers.length === 0 ? (
        <div className=" text-lg text-gray-400 font-extrabold">
          No answers added!
        </div>
      ) : (
        props.selectedQuestionItem.answers.map((itms, i) => (
          <div
            key={i + 1}
            className={`bg-white ${
              props.selectedQuestionItem.answers.length === i + 1 ? "" : ""
            } px-3 py-2 mb-2`}
          >
            <div className="flex flex-row justify-between items-center gap-2 border-b border-blue-300">
              <div
                title="Number of answer in question description"
                className="text-lg font-bold"
              >
                Reference {itms.reference}
              </div>
              {editedMarksData === null ||
              editedMarksData.reference !== itms.reference ? (
                <div
                  onClick={() => {
                    props.selectedQuestionItem.sub_question.find(
                      (itm) => itm.reference === itms.reference
                    ) &&
                      setEditedMarksData({
                        reference: itms.reference,
                        marks: props.selectedQuestionItem.sub_question
                          .find((itm) => itm.reference === itms.reference)!
                          .marks.toString(),
                      });
                    props.setShowLanguages(false);
                  }}
                  className={`flex rounded-t-xl flex-row bg-gray-100 hover:bg-blue-100 hover:text-blue-700 item-center gap-2 pl-3 cursor-pointer`}
                >
                  <div className="py-0 text-base font-semibold flex items-center justify-center w-max h-max">
                    {
                      props.selectedQuestionItem.sub_question.find(
                        (itm) => itm.reference === itms.reference
                      )?.marks
                    }{" "}
                    marks
                  </div>
                  <div className="rounded-full flex items-center justify-center h-10 w-10 cursor-pointer hover:bg-blue-100 hover:text-blue-600">
                    <MdModeEdit className="text-2xl" />
                  </div>
                </div>
              ) : (
                <div className="group flex flex-row items-center gap-2 my-1">
                  <input
                    className="input-white w-full border border-gray-600"
                    type="text"
                    placeholder="Reference marks"
                    value={editedMarksData.marks}
                    onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                      setEditedMarksData({
                        reference: itms.reference,
                        marks: e.target.value,
                      });
                    }}
                  />
                  <div
                    onClick={() => {
                      if (editedMarksData !== undefined) {
                        let newSubQuestion =
                          props.selectedQuestionItem.sub_question.find(
                            (itm) => itm.reference === editedMarksData.reference
                          );
                        if (newSubQuestion !== undefined) {
                          let newSubQuestions =
                            props.selectedQuestionItem.sub_question.filter(
                              (itm) =>
                                itm.reference !== editedMarksData.reference
                            );
                          newSubQuestions.push({
                            _id: newSubQuestion._id,
                            description: newSubQuestion.description,
                            typeShort: newSubQuestion.typeShort,
                            type: newSubQuestion.type,
                            marks: parseFloat(editedMarksData.marks),
                            reference: newSubQuestion.reference,
                          });
                          props.updateAnswerEmbedded(
                            {
                              description:
                                props.selectedQuestionItem.description,
                              language: props.selectedQuestionItem.language,
                              status: props.selectedQuestionItem.status,
                              title: props.selectedQuestionItem.title,
                              answers: props.selectedQuestionItem.answers,
                              _id: props.selectedQuestionItem._id,
                              sub_question:
                                newSubQuestions.length ===
                                props.selectedQuestionItem.sub_question.length
                                  ? newSubQuestions
                                  : props.selectedQuestionItem.sub_question,
                            },
                            itms.reference,
                            itms
                          );
                          setEditedMarksData(null);
                        }
                      }
                      props.setShowLanguages(true);
                    }}
                    className="bg-blue-600 group-hover:bg-blue-800 text-white font-bold text-base flex items-center justify-center px-3 py-1 rounded cursor-pointer"
                  >
                    Save
                  </div>
                </div>
              )}
            </div>
            <div className="flex flex-row items-center justify-between gap-2">
              <div className="flex flex-row items-center gap-3 w-full">
                <div className="w-full flex flex-col">
                  {itms.answer.map((item, t) => (
                    <div
                      key={t + 1}
                      className="group w-full flex flex-row items-center justify-between"
                    >
                      <div
                        onClick={() =>
                          (editedChoiceData === null ||
                            editedChoiceData.reference !== itms.reference ||
                            editedChoiceData.answer.id !== item.id) &&
                          updateElementStatus(itms, item)
                        }
                        className={`flex flex-row items-center gap-2 w-full
                        ${
                          FindSubQuestionData(
                            props.selectedQuestionItem.sub_question,
                            itms.reference
                          ) !== null &&
                          FindSubQuestionData(
                            props.selectedQuestionItem.sub_question,
                            itms.reference
                          )!.typeShort === QuestionForEmbedded.SingleMultiple
                            ? "border-b hover:bg-blue-50 cursor-pointer hover:border-blue-300"
                            : ""
                        }  px-2`}
                      >
                        <div className="text-3xl">
                          {FindSubQuestionData(
                            props.selectedQuestionItem.sub_question,
                            itms.reference
                          ) === null ? (
                            <div className="text-yellow-800 text-xl">
                              Invalid Answer type
                            </div>
                          ) : FindSubQuestionData(
                              props.selectedQuestionItem.sub_question,
                              itms.reference
                            )!.typeShort ===
                            QuestionForEmbedded.SingleMultiple ? (
                            <div>
                              {item.status === true ? (
                                <BsCheckCircle className="text-blue-600" />
                              ) : (
                                <BsCircle className="text-gray-400" />
                              )}
                            </div>
                          ) : (
                            <div>
                              <BsCheckCircle className="text-blue-600" />
                            </div>
                          )}
                        </div>
                        {editedChoiceData !== null &&
                        editedChoiceData.reference === itms.reference &&
                        editedChoiceData.answer.id === item.id ? (
                          <textarea
                            onChange={(
                              e: React.ChangeEvent<HTMLTextAreaElement>
                            ) => {
                              setEditedChoiceData({
                                reference: itms.reference,
                                answer: {
                                  id: item.id,
                                  value:
                                    FindSubQuestionData(
                                      props.selectedQuestionItem.sub_question,
                                      itms.reference
                                    ) === null
                                      ? ""
                                      : FindSubQuestionData(
                                          props.selectedQuestionItem
                                            .sub_question,
                                          itms.reference
                                        )!.typeShort ===
                                        QuestionForEmbedded.Numeric
                                      ? [
                                          parseFloat(e.target.value),
                                          parseFloat(item.value[1]),
                                        ]
                                      : e.target.value,
                                  status:
                                    FindSubQuestionData(
                                      props.selectedQuestionItem.sub_question,
                                      itms.reference
                                    ) === null
                                      ? ""
                                      : FindSubQuestionData(
                                          props.selectedQuestionItem
                                            .sub_question,
                                          itms.reference
                                        )!.typeShort ===
                                        QuestionForEmbedded.Numeric
                                      ? [
                                          parseFloat(e.target.value) -
                                            parseFloat(item.value[1]),
                                          parseFloat(e.target.value) +
                                            parseFloat(item.value[1]),
                                        ]
                                      : item.status,
                                },
                              });
                            }}
                            placeholder="Type answer here"
                            className="input-gray-lg w-full mr-2 my-1"
                            value={
                              FindSubQuestionData(
                                props.selectedQuestionItem.sub_question,
                                itms.reference
                              ) === null
                                ? ""
                                : FindSubQuestionData(
                                    props.selectedQuestionItem.sub_question,
                                    itms.reference
                                  )!.typeShort === QuestionForEmbedded.Numeric
                                ? editedChoiceData.answer.value[0]
                                : editedChoiceData.answer.value
                            }
                          ></textarea>
                        ) : (
                          <div
                            className={`${
                              item.status === true
                                ? "text-blue-800 font-bold"
                                : "text-gray-800"
                            } px-3 py-2 text-base`}
                          >
                            {FindSubQuestionData(
                              props.selectedQuestionItem.sub_question,
                              itms.reference
                            ) === null
                              ? ""
                              : FindSubQuestionData(
                                  props.selectedQuestionItem.sub_question,
                                  itms.reference
                                )!.typeShort === QuestionForEmbedded.Numeric
                              ? item.value[0]
                              : item.value}
                          </div>
                        )}
                      </div>
                      <div className="flex flex-row items-center gap-2">
                        <div
                          onClick={() => {
                            if (editedChoiceData === null) {
                              setEditedChoiceData({
                                reference: itms.reference,
                                answer: item,
                              });
                              props.setShowLanguages(false);
                            } else {
                              props.updateAnswerEmbedded(
                                props.selectedQuestionItem,
                                itms.reference,
                                {
                                  _id: itms._id,
                                  reference: itms.reference,
                                  answer: [editedChoiceData.answer],
                                }
                              );
                              setEditedChoiceData(null);
                              props.setShowLanguages(true);
                            }
                          }}
                          className="btn bg-blue-50 text-blue-600 group-hover:bg-blue-600 group-hover:text-white cursor-pointer"
                        >
                          Update
                        </div>
                        {FindSubQuestionData(
                          props.selectedQuestionItem.sub_question,
                          itms.reference
                        ) === null ? (
                          <div className="text-yellow-800 text-xl">
                            Invalid Answer type
                          </div>
                        ) : (
                          FindSubQuestionData(
                            props.selectedQuestionItem.sub_question,
                            itms.reference
                          )!.typeShort ===
                            QuestionForEmbedded.SingleMultiple && (
                            <div
                              onClick={() => {
                                removeElement(itms, item);
                              }}
                              className="btn bg-yellow-50 text-yellow-800 hover:bg-yellow-100 cursor-pointer"
                            >
                              Remove
                            </div>
                          )
                        )}
                      </div>
                    </div>
                  ))}

                  {FindSubQuestionData(
                    props.selectedQuestionItem.sub_question,
                    itms.reference
                  ) === null ? (
                    <div className="text-yellow-800 text-xl">
                      Invalid Answer type
                    </div>
                  ) : (
                    FindSubQuestionData(
                      props.selectedQuestionItem.sub_question,
                      itms.reference
                    )!.typeShort === QuestionForEmbedded.SingleMultiple &&
                    (addNewChoiceData === false ? (
                      <div
                        onClick={() => {
                          setNewChoiceData(null);
                          setAddNewChoiceData(true);
                        }}
                        className="btn bg-blue-50 text-blue-600 hover:bg-blue-100 cursor-pointer flex flex-row items-center gap-3"
                      >
                        <div>
                          <MdAdd className="text-3xl" />
                        </div>
                        <span className="text-base">Add new answer</span>
                      </div>
                    ) : (
                      <div className="bg-gray-100 rounded p-2 animate__animated animate__fadeIn animate__fast">
                        <div className="text-lg">Add new answer</div>
                        <div className="flex flex-row items-center mt-2">
                          <textarea
                            onChange={(
                              e: React.ChangeEvent<HTMLTextAreaElement>
                            ) => {
                              setNewChoiceData({
                                id:
                                  newChoiceData === null
                                    ? uuidv4()
                                    : newChoiceData.id,
                                value: e.target.value,
                                status:
                                  newChoiceData === null
                                    ? false
                                    : newChoiceData.status,
                              });
                              setError("");
                            }}
                            className="input-gray-lg w-full"
                            placeholder="Type new answer"
                            value={
                              newChoiceData !== null ? newChoiceData.value : ""
                            }
                          ></textarea>
                        </div>
                        <div className="flex flex-row items-center justify-between">
                          <div
                            onClick={() => {
                              setNewChoiceData(null);
                              setAddNewChoiceData(false);
                            }}
                            className="btn bg-white hover:bg-gray-200 text-gray-900 cursor-pointer mt-2 mx-1"
                          >
                            Cancel
                          </div>
                          <div className="flex flex-row items-center justify-end gap-3 pt-2">
                            <div
                              onClick={() => {
                                setNewChoiceData({
                                  id:
                                    newChoiceData === null
                                      ? uuidv4()
                                      : newChoiceData.id,
                                  value:
                                    newChoiceData === null
                                      ? ""
                                      : newChoiceData.value,
                                  status:
                                    newChoiceData !== null &&
                                    newChoiceData.status === true
                                      ? false
                                      : true,
                                });
                                setError("");
                              }}
                              className="flex flex-row items-center gap-2 bg-white rounded px-2 py-1 cursor-pointer"
                            >
                              <div>
                                {newChoiceData !== null &&
                                newChoiceData.status === true ? (
                                  <BsCheckCircle className="text-blue-600 text-3xl" />
                                ) : (
                                  <BsCircle className="text-gray-700 text-3xl" />
                                )}
                              </div>
                              <div
                                className={`text-base font-bold w-20 ${
                                  newChoiceData !== null &&
                                  newChoiceData.status === true
                                    ? "text-blue-600"
                                    : "text-gray-900"
                                }`}
                              >
                                {newChoiceData !== null &&
                                newChoiceData.status === true
                                  ? "Correct"
                                  : "Incorrect"}
                              </div>
                            </div>
                            <div
                              onClick={() => {
                                if (newChoiceData === null) {
                                  setError("Please fill answer");
                                } else {
                                  if (newChoiceData.value === "") {
                                    setError("Please fill answer");
                                  } else {
                                    props.updateAnswerEmbedded(
                                      props.selectedQuestionItem,
                                      itms.reference,
                                      {
                                        _id: uuidv4(),
                                        reference: itms.reference,
                                        answer: [newChoiceData],
                                      }
                                    );
                                    setNewChoiceData(null);
                                    setAddNewChoiceData(false);
                                    setError("");
                                  }
                                }
                              }}
                              className="btn bg-blue-600 hover:bg-blue-800 text-white cursor-pointer"
                            >
                              Add new
                            </div>
                          </div>
                        </div>
                        {error !== "" && (
                          <div
                            onClick={() => setError("")}
                            className="bg-red-100 hover:bg-red-200 text-red-800 px-3 py-2 rounded mt-2 text-base animate__animated animate__zoomIn animate__faster cursor-pointer"
                          >
                            {error}
                          </div>
                        )}
                      </div>
                    ))
                  )}
                </div>
              </div>
              {/* <div className="btn bg-blue-100 text-blue-600">Add new</div> */}
            </div>
          </div>
        ))
      )}
    </div>
  );
};

export default Embedded;
