import React, { Component } from "react";
import {
  LanguageInterface,
  QuestionAnswerInterface,
  QuestionItemInterface,
  QuestionLanguages,
} from "../../../interfaces/question";
import EditQuestion, {
  SelectedKeysEdit,
} from "../AddQuestion/UploadQuestion/EditQuestion/EditQuestion";
import { v4 as uuidv4 } from "uuid";
import { questionTypeElementsType } from "@mastrecruit/common";

interface AddQuestionManuallyProps {}

interface AddQuestionManuallyState {
  loading: boolean;
  id: string;
  course: string; // Course ID
  question: QuestionItemInterface[];
  type: questionTypeElementsType | "Invalid";
  chapters: string[]; // CourseChapter: ID
  customNumber: number;
  marks: number;
  answers: QuestionAnswerInterface[];
  selectedLanguage: LanguageInterface;
  showFilter: boolean;
  selectedKey: SelectedKeysEdit | null;
  showQuestionChapters: boolean;
  is_deleted: boolean;
}

export class AddQuestionManually extends Component<
  AddQuestionManuallyProps,
  AddQuestionManuallyState
> {
  constructor(props: AddQuestionManuallyProps) {
    super(props);

    this.state = {
      loading: false,
      id: uuidv4(),
      course: "",
      question: [],
      type: "Invalid",
      chapters: [],
      customNumber: 1,
      marks: 100,
      answers: [],
      is_deleted: false,
      selectedLanguage: QuestionLanguages[0],
      showFilter: false,
      selectedKey: null,
      showQuestionChapters: false,
    };
  }

  unSelectQuestion = () => {
    alert("Hello");
  };

  render() {
    return (
      <div>
        Add new question
        <EditQuestion
          selectedQuestion={{
            question_id: this.state.id,
            course: this.state.course,
            question: this.state.question,
            type: this.state.type,
            chapters: this.state.chapters,
            customNumber: this.state.customNumber,
            marks: this.state.marks,
            answers: this.state.answers,
            is_deleted: this.state.is_deleted,
          }}
          unSelectQuestion={this.unSelectQuestion}
        />
      </div>
    );
  }
}

export default AddQuestionManually;
