import { QuestionForHybrid } from "@mastrecruit/common";
import React, { useState } from "react";
import {
  BsCheckCircle,
  BsCheckSquareFill,
  BsCircle,
  BsSquare,
} from "react-icons/bs";
import { HiArrowNarrowRight } from "react-icons/hi";
import { ImCheckboxUnchecked } from "react-icons/im";
import { IoCheckboxOutline } from "react-icons/io5";
import {
  MatchingQuestionAnswerInterface,
  QuestionAnswerFormat,
  QuestionAnswerInterface,
  QuestionItemInterface,
} from "../../../../../../../interfaces";
import { toMatching } from "../../../../../../DoExam/QuestionDetails/ConvertingAnswers";
import { FindSubQuestionData } from "../Embedded";

interface MatchingSubQuestionsProps {
  selectedAnswer: QuestionAnswerFormat;
  itms: QuestionAnswerInterface;
  selectedQuestionItem: QuestionItemInterface;
  updateElementStatus: (
    itms: QuestionAnswerInterface,
    answer: QuestionAnswerFormat
  ) => void;
  editedChoiceData: {
    reference: number;
    answer: QuestionAnswerFormat;
  } | null;
  setEditedChoiceData: (
    data: {
      reference: number;
      answer: QuestionAnswerFormat;
    } | null
  ) => void;
  updateAnswerHybrid: (
    question: QuestionItemInterface,
    reference_number: number,
    editedAnswer: QuestionAnswerInterface
  ) => void;
  removeElement: (
    itms: QuestionAnswerInterface,
    answer: QuestionAnswerFormat
  ) => void;
  setShowLanguages: (status: boolean) => void;
}

const MatchingSubQuestions = (props: MatchingSubQuestionsProps) => {
  const [editedMatching, setEditedMatching] =
    useState<MatchingQuestionAnswerInterface | null>(
      props.editedChoiceData === null ? null : props.editedChoiceData.answer
    );
  return (
    <div className="group w-full flex flex-row items-center justify-between border-b">
      <div className="flex flex-row items-center gap-2 w-full">
        {console.log("Check edite: ", editedMatching)}
        <div
          onClick={() =>
            props.updateElementStatus(props.itms, props.selectedAnswer)
          }
          className="text-3xl"
        >
          {FindSubQuestionData(
            props.selectedQuestionItem.sub_question,
            props.itms.reference
          ) === null ? (
            <div className="text-yellow-800 text-xl">Invalid Answer type</div>
          ) : (
            <div>
              <BsCheckCircle className="text-blue-600" />
            </div>
          )}
        </div>
        {editedMatching !== null ? (
          <div className="w-full mr-2">
            <textarea
              onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) => {
                setEditedMatching({
                  id: props.selectedAnswer.id,
                  value: editedMatching === null ? "" : e.target.value,
                  status: editedMatching === null ? "" : editedMatching.status,
                });
              }}
              placeholder="Type answer here"
              className="input-gray-lg w-full mr-2 mt-1"
              value={editedMatching === null ? "" : editedMatching.value}
            ></textarea>
            <textarea
              onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) => {
                setEditedMatching({
                  id: props.selectedAnswer.id,
                  value: editedMatching === null ? "" : editedMatching.value,
                  status: editedMatching === null ? "" : e.target.value,
                });
              }}
              placeholder="Type answer here"
              className="input-gray-lg w-full mr-2 mb-1"
              value={editedMatching === null ? "" : editedMatching.status}
            ></textarea>
          </div>
        ) : (
          <div>
            <div className={`text-gray-800 px-3 pt-2 text-base`}>
              {FindSubQuestionData(
                props.selectedQuestionItem.sub_question,
                props.itms.reference
              ) === null
                ? ""
                : props.selectedAnswer.value}
            </div>
            <div
              className={`text-blue-600 font-bold px-3 pb-2 text-base flex flex-row items-center gap-2`}
            >
              <div>
                <HiArrowNarrowRight className="text-3xl" />
              </div>
              <div>
                {FindSubQuestionData(
                  props.selectedQuestionItem.sub_question,
                  props.itms.reference
                ) === null
                  ? ""
                  : props.selectedAnswer.status}
              </div>
            </div>
          </div>
        )}
      </div>
      <div className="flex flex-row items-center gap-2">
        <div
          onClick={() => {
            if (editedMatching === null) {
              props.setEditedChoiceData({
                reference: props.itms.reference,
                answer:
                  props.editedChoiceData !== null
                    ? props.editedChoiceData.answer
                    : props.selectedAnswer,
              });
              props.editedChoiceData !== null
                ? setEditedMatching(props.editedChoiceData.answer)
                : setEditedMatching(props.selectedAnswer);
              props.setShowLanguages(false);
            } else {
              props.updateAnswerHybrid(
                props.selectedQuestionItem,
                props.itms.reference,
                {
                  _id: props.itms._id,
                  reference: props.itms.reference,
                  answer: [editedMatching],
                }
              );
              setEditedMatching(null);
              props.setEditedChoiceData(null);
              props.setShowLanguages(true);
            }
          }}
          className="px-3 py-1 rounded bg-blue-50 text-blue-600 group-hover:bg-blue-600 group-hover:text-white cursor-pointer text-center"
        >
          Update
        </div>
        {FindSubQuestionData(
          props.selectedQuestionItem.sub_question,
          props.itms.reference
        ) === null ? (
          <div className="text-yellow-800 text-xl">Invalid Answer type</div>
        ) : (
          <div
            onClick={() => {
              props.removeElement(props.itms, props.selectedAnswer);
            }}
            className="px-3 py-1 rounded bg-yellow-50 text-yellow-800 hover:bg-yellow-100 cursor-pointer text-center"
          >
            Remove
          </div>
        )}
      </div>
    </div>
  );
};

export default MatchingSubQuestions;
